.page {
  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__productCard {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-200%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
